body {
  margin: 0;
  overflow-y: hidden;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
    background-color: rgba(190, 200, 205, 0.665);
    height: 100vh;
    padding-top: 10px;
    font-family: Lato, sans-serif;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.contenedor-titulo {
    width: 100%;
    margin: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(162, 232, 236, 0.394);
    color: rgb(47, 171, 224);
    border-radius: 50px 0px 50px;
    padding: 10px;
    box-shadow: 15px 15px 50px #1a062e72;

}
.contenedor-cacl{

    width: 400px;
    height: 600px;
    padding: 14px;
    background-color: rgb(224, 224, 224);
    border-radius: 20px;
    border: 5px solid white;
    box-shadow: 30px 20px 20px gray;
}
.fila{
    margin: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
}




.boton-contenedor{
    display: flex;
    flex: 1 1;
    
    align-items: center;
    justify-content: center;
    
    width: 100%;
    height: 70px;
    margin: 5px;

    font-weight: bold;
    font-size: 38px;

    background-color: rgb(109, 115, 132);
    color: white;

    border-radius: 25%;
    border: 2px solid #ccc;

    
    cursor: pointer;
    -webkit-user-select: none;
            user-select: none;

}

.boton-contenedor:hover{
  background-color: rgba(57, 58, 58, 0.25);
}
.operador{
    background-color: rgb(255, 138, 70);
}

.operador:hover{
  background-color: rgba(243, 195, 107, 0.813);
}


.input{

    display: flex;

    align-items: center;
    justify-content: flex-end;

    height: 75px;
    padding: 11px 30px 11px 11px; 
    margin-bottom: 20px;

    font-weight: bold;
    font-size: 30px;
    background-color: #888;
    color: white;

    border-radius: 10px;
    border: 3px solid #ccc;
    box-shadow: -1px 4px 1px 1 px white;
}
.clear{

    display: flex;

    
    align-items: center;
    justify-content: center;
    
    height: 80px;
    width: 100%;
    margin: 5px;

    font-weight: bold;
    font-size: 38px;

    background-color:rgba(255, 0, 0, 0.416);
    color: white;

    border-radius: 30px;
    border: 2px solid #ccc;

    
    cursor: pointer;
    -webkit-user-select: none;
            user-select: none;
}
.clear:hover{
    background-color: rgba(255, 0, 0, 0.744);
}
